.cta {
  position: relative;
  padding: 250px 0;
  background-image: url("images/bg-cta.jpg");
  background-position: center;
  background-size: cover; }
  .cta .cta-content {
    position: relative;
    z-index: 1; }
    .cta .cta-content h2 {
      font-size: 50px;
      max-width: 450px;
      margin-top: 0;
      margin-bottom: 25px;
      color: white; }
    @media (min-width: 768px) {
      .cta .cta-content h2 {
        font-size: 80px; } }
  .cta .overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5); }



