/* Styling for the masthead */
.masthead {
  position: relative;
  width: 100%;
  padding-top: 150px;
  padding-bottom: 100px;
  color: white;
  background: url("./images/bg-pattern.png"), #7b4397;
  background: url("./images/bg-pattern.png"), -webkit-linear-gradient(to left, #7b4397, #dc2430);
  background: url("./images/bg-pattern.png"), -webkit-gradient(linear, right top, left top, from(#7b4397), to(#dc2430));
  background: url("./images/bg-pattern.png"), -webkit-linear-gradient(right, #7b4397, #dc2430);
  background: url("./images/bg-pattern.png"), -o-linear-gradient(right, #7b4397, #dc2430);
  background: url("./images/bg-pattern.png"), linear-gradient(to left, #7b4397, #dc2430); }
  .masthead .header-content {
    max-width: 500px;
    margin-bottom: 100px;
    text-align: center; }
    .masthead .header-content h1 {
      font-size: 30px; }
  .masthead .device-container {
    max-width: 325px;
    margin-right: auto;
    margin-left: auto; }
    .masthead .device-container .screen img {
      border-radius: 3px; }
  @media (min-width: 992px) {
    .masthead {
      height: 100vh;
      min-height: 775px;
      padding-top: 0;
      padding-bottom: 0; }
      .masthead .header-content {
        margin-bottom: 0;
        text-align: left; }
        .masthead .header-content h1 {
          font-size: 50px; }
      .masthead .device-container {
        max-width: 325px; } }



